h1 {
    font-family: sans-serif;
    font-size: 56px;
    padding: 5px;
    padding-left: 15px;
    padding-right: 15px;
    margin: 0;
    background: -webkit-gradient(linear, 
                                left top, left bottom, 
                                from(white),
                                color-stop(62%, white), 
                                color-stop(62%, gold), 
                                to(gold));
    background: -webkit-linear-gradient(top, 
                                white 0%,
                                white 62%, 
                                gold 62%, 
                                gold 100%);
    background: -o-linear-gradient(top, 
                                white 0%,
                                white 62%, 
                                gold 62%, 
                                gold 100%);
    background: linear-gradient(to bottom, 
                                white 0%,
                                white 62%, 
                                gold 62%, 
                                gold 100%);
}


